@import '~assets/styles/h3Imports';

.pop-over-containter {
  background-color: var(--pop-over-color-background);
  border-radius: var(--pop-over-border-radius, 8px);
  box-shadow: 0 rem(5) rem(14) var(--pop-over-box-shadow-color);
  list-style-type: none;
  padding: 0;
  position: absolute;
  width: var(--pop-over-width, 10.1875rem); // 163px
  z-index: 10;

  @media #{$large-up} {
    width: var(--pop-over-width-large, var(--pop-over-width, 16rem)); // 265px
  }

  .pop-over-containter {
    display: none;
    left: calc(100% + 8px);
    top: 0;

    &:focus-within,
    &[data-showing='true'] {
      display: block;
    }
  }
}

.pop-over-icon-left,
[data-element='avatar'] {
  display: flex;
  flex-direction: column;
  justify-content: center;

  > span {
    margin-right: rem(8);
  }
}

.pop-over-title {
  @include paragraph;

  display: inline-block;
  flex: 0 1 100%;
  margin: 0;
}

.pop-over-containter li {
  height: 100%;
  margin: 0;
  min-height: rem(56);
  position: relative;
  width: 100%;

  &[data-sub='true'] {
    &::after {
      background: transparent;
      content: '';
      height: 100%;
      left: 100%;
      position: absolute;
      top: 0;
      width: 0.5rem;
    }

    &:hover {
      .pop-over-containter {
        display: block;
      }
    }
  }

  a {
    @media #{$large-up} {
      height: rem(56);
    }

    align-content: center;
    color: var(--gray-5);
    display: flex;
    min-height: rem(56);
    padding: rem(16) rem(16);
    text-decoration: none;
    width: 100%;
  }

  &[has-divider='true'] {
    border-bottom: rem(1) solid rgba(50, 50, 100, 0.16);
  }

  &[element-type='title'] {
    height: rem(44);
    min-height: rem(44);

    a {
      height: rem(44);
      min-height: rem(44);
      padding: rem(12) rem(16);
    }

    .pop-over-title {
      color: var(--pop-over-title);
      font-size: rem(14);
      font-weight: 600;
      line-height: rem(20);
    }
  }

  &[element-type='avatar'] .pop-over-icon-left {
    height: rem(32);

    figure {
      bottom: rem(4);
      margin-right: rem(8);
    }
  }

  &[element-type='blue'] .pop-over-title {
    color: var(--interface-info);
  }

  &[element-type='red'] .pop-over-title {
    color: var(--pop-over-red);
  }

  &[element-type='bold'] .pop-over-title,
  &[element-type='A'] .pop-over-title {
    font-weight: 600;
    line-height: rem(24);
  }

  &[element-type='AA'] .pop-over-title {
    font-size: rem(19);
    font-weight: 700;
    line-height: rem(24);
  }

  &[element-type='AAA'] {
    a {
      padding: rem(12) rem(16);
    }

    .pop-over-title {
      font-size: rem(27);
      font-weight: 700;
      line-height: rem(32);
    }
  }
}

.pop-over-icon-right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  justify-self: flex-end;
  margin-left: rem(16);
}

li > h2.pop-over-title {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  padding: rem(16) rem(16);
}
