@import '~assets/styles/h3Imports';

@mixin menuitem-popover {
  @include paragraph;

  align-items: center;
  background-color: transparent;
  border: none;
  display: flex;
  transition: background-color 0.2s;
  width: 100%;

  &:hover,
  &:focus,
  &[aria-checked='true'] {
    @include interactive-text;

    background-color: var(--profile-menu-pop-selected-hover-background);
  }

  &:focus-visible {
    @include interactive-text;

    background-color: var(--profile-menu-pop-selected-hover-background);
  }

  & span[data-menuitem-text] {
    flex-grow: 1;
    margin-left: rem(8);
    text-align: left;
  }

  figure {
    --avatar-bg: var(--profile-menu-pop-avatar-background);

    span {
      color: var(--profile-menu-pop-avatar-color);
    }
  }
}

.trigger-button {
  & > p {
    display: none;
  }

  & > span svg {
    margin-left: 13px;
  }
}

.container {
  display: inline-block;
  position: relative;
}

.popover-container {
  max-width: calc(100vw - 32px);
  position: absolute;
  right: 0;
  width: rem(256);

  ul {
    margin: 0;
    width: 100%;
  }

  li {
    min-height: 0;
  }
}

.title {
  @include caption-text;

  color: var(--profile-menu-pop-title-color);
  margin: 0;
  padding: 12px 16px;
}

.connected-profiles {
  @include menuitem-popover;

  @media (max-width: 500px) {
    [data-fontsize='AAA'] & {
      font-size: 0.85rem;
    }
  }

  padding: 12px 16px;

  span > svg {
    width: rem(16);
  }

  span[aria-hidden='true'] {
    align-items: center;
    display: flex;
    height: rem(12);
  }
}

.manage-profiles {
  @include menuitem-popover;

  color: var(--profile-menu-manage-profiles-color);
  padding: 16px;

  --icon-stroke: var(--profile-menu-manage-profiles-color);

  span {
    display: flex;
  }
}

.sign-out {
  @include menuitem-popover;

  color: var(--profile-menu-sign-out-color);
  padding: 16px;

  --icon-stroke: var(--profile-menu-sign-out-color);

  span {
    display: flex;
  }
}

@media #{$tablet-up} {
  .trigger-button {
    & > p {
      display: flex;
    }

    & > span svg {
      margin-left: 0;
    }
  }
}

.fieldset {
  border: none;
  margin: 0;
  padding: 0;
}

.profiles {
  margin: 0;
  padding: 0;

  > li {
    list-style-type: none;

    &::before {
      content: '\200B';
      position: absolute;
    }
  }
}
